/* CSS Document */
* {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
body,
p,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
dl,
dd,
td,
input,
fieldset,
textarea,
option {
    margin: 0;
    padding: 0;
}
li {
    list-style: none;
}
a {
    text-decoration: none;
    color: #333;
}
img {
    border: none;
    vertical-align: top;
}
a,
input {
    outline: none;
}
h1,
h2,
h3,
h4,
h5,
h6,
b,
strong {
    font-weight: 100;
}
i,
em {
    font-style: normal;
}
body {
    font: 12px "\5FAE\8F6F\96C5\9ED1";
}
html,
body {
    width: 100%;
    background: #f7f7f7;
}
.clear {
    zoom: 1;
}
.clear:after {
    content: ".";
    display: block;
    clear: both;
    height: 0;
    visibility: hidden;
}
.fl {
    float: left;
}
.fr {
    float: right;
}
.wrap {
    position: relative;
    width: 100%;
}
.nav-box {
    width: 100%;
    background: #fff;
    padding: 43px 0;
}
.top-nav {
    width: 1440px;
    margin: 0 auto;
}
.top-nav .nav-list .nav-item {
    float: left;
    position: relative;
    font-size: 18px;
    margin: 0 5px;
    padding: 20px 0;
}
.top-nav .nav-list .nav-item:last-child {
    margin-right: -20px;
}
.top-nav .nav-list .spot {
    display: inline-block;
    width: 8px;
    height: 8px;
    border-radius: 100% 100%;
    background: #fff;
}
.top-nav .nav-list a {
    margin: 0 5px;
    color: #262626;
}
.top-nav .nav-list .nav-item:hover a {
    color: #ff532a;
}
.top-nav .nav-list .nav-item.active .spot {
    background: #ff532a;
}
.top-nav .nav-list .nav-item.active .arc {
    display: block;
}
.top-nav .nav-list .nav-item.active a {
    color: #ff532a;
}
.banner {
    width: 100%;
}
.main {
    width: 1440px;
    margin: 30px auto;
}
.container {
    width: 1440px;
    margin: 0 auto;
}
.top-bar-box {
    width: 100%;
}
.top-bar-box .top-bar {
    width: 1440px;
    height: 50px;
    line-height: 50px;
    margin: 0 auto;
    font-size: 14px;
    color: #333;
}
.top-bar-box .top-bar .switch-language li {
    display: inline;
    cursor: pointer;
}
.top-bar-box .top-bar .switch-language li span {
    margin-left: 6px;
}
.top-bar-box .top-bar .switch-language .line {
    margin: 0 18px;
    color: #ebebeb;
}
.footer {
    width: 100%;
    background: #fff;
}
.footer .footer-box {
    width: 1440px;
    margin: 0 auto;
}
.footer .footer-box .site-info .site-list {
    margin: 70px 0;
    text-align: left;
    border-right: 1px solid #e6e6e6;
    overflow: hidden;
}
.footer .footer-box .site-info .site-list .site-item {
    float: left;
    width: 240px;
}
.footer .footer-box .site-info .site-list .site-item p {
    font-size: 16px;
    color: #1a1a1a;
}
.footer .footer-box .site-info .site-list .site-item a,
.footer .footer-box .site-info .site-list .site-item span {
    display: table;
    font-size: 14px;
    color: rgba(51, 51, 51, 0.5);
}
.footer .footer-box .site-info .site-list .site-item a:hover {
    color: #ff532a;
}
.footer .footer-box .site-info .site-list .site-item .item-center {
    margin: 36px 0 12px 0;
}
.footer .footer-box .site-info .download {
    text-align: center;
}
.footer .footer-box .site-info .download .download-tip {
    font-size: 12px;
    color: #1a1a1a;
    margin: 55px 0 10px 0;
}
.footer .footer-box .site-info .download .download-code {
    width: 90px;
    height: 90px;
}
.footer .footer-box .site-info .download .download-code img {
    width: 100%;
    height: 100%;
}
.footer .footer-bar {
    width: 100%;
    background: #333;
}
.footer .footer-bar .footer-box {
    position: relative;
    width: 1440px;
    height: 70px;
    line-height: 70px;
    font-size: 16px;
    color: #737373;
    margin: 0 auto;
    text-align: center;
}
.footer .footer-bar .footer-box .footer-links {
    position: absolute;
    top: 0;
    left: 0;
}
.footer .footer-bar .footer-box .copy-right {
    position: absolute;
    top: 0;
    right: 0;
    font-size: 14px;
}
