    color: #1a1a1a;
    line-height: 30px;
    margin-bottom: 20px;
}
.main .main-top .main-tip {
    display: block;
    width: 800px;
    margin: 0 auto;
    font-size: 14px;
    line-height: 18px;
    color: #999;
}
.main .main-center {
    -js-display: flex;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    margin: 30px auto 120px;
}
.main .main-center .item {
    font-size: 16px;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    text-align: center;
}
.main .main-center .item .flow-chart {
    display: block;
    width: 244px;
    height: 424px;
    margin: 0 auto;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
}
.main .main-center .item .step {
    line-height: 30px;
    color: #999999;
}
.main .main-center .item .step-info {
    line-height: 30px;
    color: #333333;
}
.main .main-center .item .spot {
    margin-top: 195px;
    margin-top: 0\9;
}
#slider {
    width: 100%;
}
.slider_box {
    width: 100%;
    overflow: hidden;
    position: relative;
}
.silder_con {
    width: 100%;
    height: 696px;
    overflow: hidden;
    position: relative;
}
.silder_panel {
    max-width: 1903px;
    height: 696px;
    overflow: hidden;
    float: left;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
.silder_panel img {
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: block;
}
.silder_nav {
    width: 100%;
    min-height: 210px;
    margin-top: 5px;
    overflow: hidden;
    position: relative;
}
.silder_nav li {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    float: left;
    width: 19.8%;
    margin-right: 0.18%;
    height: 100%;
    overflow: hidden;
}
.silder_nav li:last-child {
    margin-right: 0;
}
.silder_nav li img {
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: block;
}
.slider_box a.prev,
.slider_box a.next {
    width: 50px;
    height: 100px;
    bottom: 70px;
    text-indent: -999px;
    z-index: 100;
    overflow: hidden;
    display: block;
    position: absolute;
    cursor: pointer;
    background-size: 100%;
}
.slider_box a.prev {
    left: 20px;
}
.slider_box a.next {
    right: 20px;
}
.main-left-area {
    margin-right: 460px;
    font-size: 14px;
}
.main-right-area {
    float: right;
    width: 460px;
    padding-left: 16px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.main-right-area>div {
    margin-bottom: 16px;
}
/* 小标题 */
.sub-nav {
    background-color: rgb(51, 51, 51);
    color: #737373;
    line-height: 46px;
}
.sub-nav-list>li {
    display: inline-block;
    font-size: 18px;
    width: 150px;
    text-align: center;
}
.sub-nav-list>li a {
    color: #737373;
    display: inline-block;
    width: 100%;
}
.sub-nav-list>li.active {
    background-color: rgb(255, 56, 0)
}
.sub-nav-list>li.active a {
    color: #fff;
}
/* 横幅 */
.banner {
}
.banner-small {
    background-color: #eeeeee;
    line-height: 100px;
}
.banner-small .title {
    font-size: 26px;
    font-weight: bold;
    vertical-align: middle;
    margin: 0 8px;
}
.banner-small .text {
    font-size: 16px;
    vertical-align: middle;
}
.banner-small img {
    vertical-align: middle;
}
/* 文章 */
.article {
    margin: 16px 0;
    font-size: 14px;
    color: #666;
}
.article-section {
    margin-bottom: 24px;
}
.article-section-paragraph {
    margin-bottom: 8px;
}
.article-section-paragraph::after {
    content: '';
    width: 0;
    height: 0;
    clear: both;
}
.article-section-title {
    font-weight: bold;
    font-size: 16px;
    color: #000;
    margin-bottom: 8px;
}
.article-section-name {
    margin-right: 8px;
    float: left;
}
.article-section-content {
    margin-left: 50px;
}
.article-section-content>p {
    margin-bottom: 8px;
}
/* 奖金对照表 */
.main-right-area .title {
    font-size: 14px;
    border-bottom: 1px solid #ff3800;
    font-weight: bold;
}
.dispaly-table {
    width: 100%;
    border: 1px solid #dcdcdc;
    margin: 8px 0;
    border-collapse: collapse;
}
.dispaly-table th,
.dispaly-table td {
    border: 1px solid #dcdcdc;
    border-collapse: collapse;
    text-align: center;
    line-height: 40px;
}
.dispaly-table th {
    background-color: #dcdcdc;
    border-color: #fff;
}
.circle-wrapper {
    line-height: 22px;
    width: 120px;
    display: inline-block;
    vertical-align: middle;
    margin: 4px 0;
}
.circle {
    display: inline-block;
    width: 14px;
    height: 14px;
    background: url(data:image/gif;base64,) no-repeat;
}
.circle.orange {
    background-position: 0 -28px;
}
.circle.blue {
    background-position: 0 -14px;
}
/* 热度排行 */
.ranking {
}
.ranking-section,
.news-section {
    background-color: #fff;
    padding: 16px 24px;
    line-height: 22px;
}
.ranking-section-title {
    font-size: 14px;
    font-weight: bold;
}
.ranking-section-content,
.news-section-content {
    color: #777777;
    line-height: 24px;
}
/* 动态 */
.article-list-title {
    float: left;
    color: #1a1a1a;
    font-size: 16px;
}
.article-list-title:hover>a{
    cursor: pointer;
    color: #2380cf;
}
.article-list-date {
    float: right;
    color: #999999;
    font-size: 14px;
}
.article-list-section {
    line-height: 32px;
    height: 32px;
}
.article-list {
    padding: 24px;
    background-color: #fff;
}
.article-list-page {
    margin-top: 16px;
}
.page-btn {
    display: inline-block;
    border: 1px solid #d9d9d9;
    color: #39a0ff;
    padding: 8px;
    margin-right: 8px;
    min-width: 34px;
    text-align: center;
    cursor: pointer;
}
.page-btn.active {
    background-color: #f7f7f7;
    color: #000;
    cursor: default;
}
/* 视频页 */
.video-list {
    margin-right: 1%;
}
.video-list-item {
    float: left;
    margin-right: 1%;
    width: 24%;
}
.video-title {
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 24px;
    font-size: 14px;
    color: #8a8a8a;
    padding: 16px 0;
}
.video-thumbnail {
    position: relative;
}
.video-thumbnail:hover .video-thumbnail-btn,
.video-thumbnail:hover .video-thumbnail-cover {
    display: block;
}
.video-thumbnail-btn {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-image: url(data:image/png;base64,);
    width: 52px;
    height: 52px;
    margin: auto;
    z-index: 2;
}
.video-thumbnail-cover {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-image: url(data:image/png;base64,);
    z-index: 1;
}
.video-thumbnail-img {
    display: inline-block;
    height: 120px;
}
.video-detail-title {
    margin: 16px;
    margin-top: 32px;
    text-align: center;
    font-size: 18px;
}
.video-detail-content {
    height: 600px;
    background-color: #262a2d;
    position: relative;
}
.video-player {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 860px;
    height: 483px;
    background-color: #191a1d;
}
.video-detail-list {
    margin: 48px;
    white-space: nowrap;
    overflow: hidden;
}
.video-detail-item {
    display: inline-block;
    width: 160px;
    height: 90px;
    margin-right: 16px;
}
.video-detail-list-lbtn {
    float: left;
}
.video-detail-list-rbtn {
    float: right;
}
.video-detail-list-lbtn,
.video-detail-list-rbtn {
    height: 120px;
    width: 32px;
    background-color: #909090;
    cursor: pointer;
    position: relative;
}
.video-detail-list-lbtn:hover,
.video-detail-list-rbtn:hover {
    background-color: #262a2d;
}
.video-detail-list-lbtn:after {
    content: '';
    width: 0;
    height: 0;
    position: absolute;
    border-top: 10px solid transparent;
    border-right: 8px solid #fff;
    border-bottom: 10px solid transparent;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
}
.video-detail-list-rbtn:after {
    content: '';
    width: 0;
    height: 0;
    position: absolute;
    border-top: 10px solid transparent;
    border-left: 8px solid #fff;
    border-bottom: 10px solid transparent;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
}
/* 新闻 */
.main-banner {
    height: 300px;
